.link-base() {
	.button-base();

	color: @darkest;
	text-align: start;
	background-color: transparent;

	&:hover {
		opacity: 0.6;
	}

	&:active {
		opacity: 0.3;
	}
}

.link {
	.link-base();
}

.link--goals {
	.link-base();

	&:hover {
		opacity: 1;
	}

	&:active { /* повторяю из примеси, тк active дб ниже hover */
		opacity: 0.3;
	}
}

.link--progress-bar {
	.link-base();

	font-size: 16px;
	line-height: 20px;
	border: transparent;
}
